import React, { Component } from "react";

export default class Carousel extends Component {
  static defaultProps = {
    defaultDescription: [
      <span>Blocktags are available in multiple form factors, materials, and security levels. Custom configurations welcome.</span>,
      <span class="italic text-gray-500">hover on tags for info</span>
    ],
    data: []
  }

  constructor(props) {
    super(props)
    this.state = {
      activeEl: null
    }

    this.onMouseEnter = this.onMouseEnter.bind(this);
    this.onMouseLeave = this.onMouseLeave.bind(this);
  }

  onMouseEnter(i) {
    return event => {
      this.setState({activeEl: i})
    }
  }

  onMouseLeave(event) {
    this.setState({activeEl: null})
  }

  render() {
    const {activeEl} = this.state
    const data = this.props.data

    return (
      <div>
        <div className="flex flex-wrap py-12 mt-32 items-end">
          {this.props.data.map((c, i) => (
            <div key={`carousel_${i}`}
              className={(activeEl!==null? "hover:opacity-100 opacity-50":"first:opacity-50 last:opacity-50")+ " w-1/5 text-center transition ease-out duration-700 transform hover:scale-125"}
              onMouseEnter={this.onMouseEnter(i)}
              onMouseLeave={this.onMouseLeave}>
              <img className="inline" style={{maxWidth: "100px", maxHeight:"300px"}} src={c.img} />
            </div>
          ))}
        </div>
        <div className="w-full my-24 text-center justify-center text-sm">
          <p className="max-w-md mx-auto" style={{minHeight:"3rem"}}>{activeEl !== null ? data[activeEl].description : this.props.defaultDescription}</p>
        </div>
      </div>
    )
  }
}
